@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.logoDiv{display:flex;flex-direction:row;justify-content:space-between;background-color:rgba(0,0,0,0.9);color:white;border-bottom:2px solid #425b5e;font-family:'Bree Serif', serif}.logoDiv .logoName{padding-top:8px;font-size:30px}.logoDiv .logo{display:flex;flex-direction:row;justify-content:flex-start}.logoDiv .topRight{display:flex;flex-direction:row;justify-content:space-around}.logoDiv .topRight .navLinks{display:flex;flex-direction:row;align-items:center;list-style:none;text-decoration:none;color:white}.logoDiv .topRight .navLinks .navItem{text-decoration:none;color:white;margin-right:20px;margin-left:20px;padding-top:20px;font-size:20px;font-family:'Montserrat', sans-serif}.logoDiv .topRight .navLinks .navItem:hover{color:blue}.logoDiv .topRight .timerDisplay{margin-left:20px;margin-right:10px;border:5px solid blue;border-radius:10px;padding-left:10px;width:120px;font-family:'Montserrat', sans-serif}.App-logo{height:100px;pointer-events:none;margin-left:15px}@media screen and (min-width: 900px){.logoDiv .logoName{font-size:35px}}

.search{background-image:url(/News-ReactApp/static/media/planet.c9ce145e.jpg);text-align:center;height:auto;padding:20px;display:flex;flex-direction:column;flex-wrap:nowrap;justify-content:center;align-items:center;font-family:'Cinzel', serif}.search h1,.search h2{color:white}.searchBar{border:2px solid grey;border-radius:4px;width:60%;height:50px;font-size:20px;padding:10px}@media screen and (min-width: 900px){.searchBar{width:50%}}

.card{background-color:white;height:auto;width:300px;margin:10px}.card img{width:300px;height:auto}.card h1,.card p,.card h5{padding:10px}

.empty{background-color:#f2f2f3;height:40vmin;display:flex;flex-direction:column;align-items:center}

.newsDisplayDiv{height:auto;background-color:#c0c0c0;display:flex;flex-direction:row;justify-content:center;flex-wrap:wrap}

.footerDiv{background-color:#0d0202;border:2px solid black;color:rgba(255,255,255,0.4);padding:0;display:flex;flex-direction:row;justify-content:space-between}.footerDiv p{padding:10px}

